function MyDataTable() {
  const [filteredData, setFilteredData] = useState(data);

  const handleSearch = value => {
    const filteredItems = data.filter(item =>
      item.carpeta.toLowerCase().includes(value.toLowerCase()) ||
      item.titulo.toLowerCase().includes(value.toLowerCase()) ||
      item.ambito.toLowerCase().includes(value.toLowerCase()) ||
      item.estado.toLowerCase().includes(value.toLowerCase())
    );
    setFilteredData(filteredItems);
  };

  // Define las columnas y establece el ancho de la columna del ttulo y carpeta
  const columns = [
    {
      name: 'Carpeta',
      selector: row => row.carpeta,
      sortable: true,
      cell: row => <div style={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>{row.carpeta}</div>, // Renderiza el texto completo de la carpeta y permite envolver palabras
    },
    {
      name: 'Ttulo del documento',
      selector: row => row.titulo,
      sortable: true,
      cell: row => <div style={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>{row.titulo}</div>, // Renderiza el texto completo del ttulo y permite envolver palabras
    },
    {
      name: 'mbito',
      selector: row => row.ambito,
      sortable: true,
    },
    {
      name: 'Estado',
      selector: row => row.estado,
      sortable: true,
    },
    {
      name: 'Enlace de descarga',
      cell: row => {
        if (row.estado !== 'No disponible' && row.enlace) {
          return <a href={row.enlace} target="_blank" rel="noopener noreferrer">Descargar</a>;
        } else {
          return null; // No renderiza ningn contenido si el estado es "No disponible" o si no hay enlace
        }
      }
    }
    // Puedes aadir ms columnas segn necesites
  ];

  return (
    <Container>
      <h1 style={{ fontSize: '24px', marginBottom: '15px' }}>Buscar un documento genrico</h1>
      <input
        type="text"
        className="form-control mb-3"
        placeholder="Buscar..."
        onChange={e => handleSearch(e.target.value)}
      />
      <DataTable
        columns={columns}
        data={filteredData}
        pagination
      />
    </Container>
  );
}

export default MyDataTable;